#root {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

html {
	background: #fff;
}

html,
body,
canvas {
	touch-action: none;
	overscroll-behavior: none;
}

.hover-label {
	position: absolute;
	opacity: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	height: 50px;
	left: 0;
	top: 0;
	transition: opacity 1.2s ease, transform 0.3s ease;
}

.hover-label.visible {
	opacity: 1; /* fades in */
}

.hover-label > div {
	width: 100%;
	height: 100%;
}

.hover-label button {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	border: none;
	cursor: pointer;
	background: linear-gradient(to bottom, #fefefe, #e5e5e5); /* soft gloss gradient */
	box-shadow: inset 0 1px 2px rgba(255, 255, 255, 0.8), /* subtle inner highlight */ inset 0 -2px 4px rgba(0, 0, 0, 0.15),
		/* inner rim at bottom */ 0 4px 8px rgba(0, 0, 0, 0.25); /* outer drop shadow */
	position: relative;
	transition: all 0.2s ease;
	font-size: 2rem;
	font-weight: 500;
	color: #333; /* Apple uses subtle dark gray instead of black */
}

.hover-label button svg {
	width: 23px;
	height: 23px;
	pointer-events: none;
}

/* Glossy top overlay */
.hover-label button::before {
	content: "";
	position: absolute;
	top: 4px;
	left: 4px;
	right: 4px;
	height: 40%;
	border-radius: 50%;
	background: radial-gradient(circle at top, rgba(255, 255, 255, 0.9), transparent);
	pointer-events: none;
}

/* Pressed state */
.hover-label button:active {
	background: linear-gradient(to bottom, #e0e0e0, #cfcfcf);
	box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.2), /* stronger inset */ 0 2px 4px rgba(0, 0, 0, 0.2);
	transform: translateY(1px);
}
